import React, { Component } from 'react';

import { Button } from './Button.js';

export class NewGameForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
     inputValue: ""
    };

    this.handleSubmitPlayer = this.handleSubmitPlayer.bind(this)
    this.updateInputValue = this.updateInputValue.bind(this)
  }

  updateInputValue(e) {
    this.setState({
      inputValue: e.target.value
    })
  }

  handleSubmitPlayer() {
    this.props.onSubmitPlayer(this.state.inputValue)
    this.setState({
      inputValue: ""
    })
  }

  render() {
    return (
      <div className="column content">
        <h2>Add player</h2>

        <div className="field has-addons">
          <div className="control">
            <input className="input" type="text" placeholder="Player name" value={this.state.inputValue} onChange={this.updateInputValue}/>
          </div>
          <div className="control">
            <Button onClick={this.handleSubmitPlayer}>Add player</Button>
          </div>
        </div>
      </div>
    );
  }
}



