import React, { Component } from 'react';

import { Match } from './Match.js';

export class Heat extends Component {

  // var chunkSize;

      // if (subArr.length % 3 === 0) {
      //   chunkSize = 3
      // } else {
      //   chunkSize = 4
      // }

      // var results = [];
      // while (subArr.length) {
      //   results.push(subArr.splice(0, chunkSize));
      // }


  render() {
    var counter = 0
    // var matches = this.props.players.map(function(match) {
    //         counter ++
    //         return <Match key={counter} heatId={this.props.nr} markWinner={this.props.markWinner} nr={counter} players={match}/>
    //       }.bind(this))

    var chunkSize;

    var players = this.props.players.slice(0)

    if (players.length % 3 === 0) {
      chunkSize = 3
    } else {
      chunkSize = 4
    }

    var results = [];
    while (players.length) {
      results.push(players.splice(0, chunkSize));
    }

    var matches = results.map(function(match) {
      counter ++
      return <Match key={counter} heatId={this.props.nr} markWinner={this.props.markWinner} nr={counter} players={match}/>
    }.bind(this))

    var space = this.props.nr > 1 ? <br/> : ""

    return (
      <section>
        { space }
        <h2>Heat #{this.props.nr}</h2>
        <div className="columns">
          {matches}
        </div>
      </section>
    );
  }
}



