import React, { Component } from 'react';

import { Heat } from './Heat.js';

export class CurrentGame extends Component {
  constructor(props) {
    super(props);

    this.state = {
      players: []
    }

    this.markWinner = this.markWinner.bind(this)
  }


  markWinner(heatId, playerName) {
    console.log("marking winner")
    console.log(this.state.players)


    var newPlayers = this.state.players.slice(0)

    if (newPlayers[heatId]) {
      newPlayers[heatId].push(playerName)
    } else {
      newPlayers.push([playerName])
    }

    console.log("newplayers")
    console.log(newPlayers)

    this.setState({ players: newPlayers})
    // this.chunking()
  }

  componentDidMount() {
    console.log("mount props")
    console.log(this.props.initialPlayers)
    console.log("mount state")
    console.log(this.state.players)
    this.setState({
      players: [this.props.initialPlayers.slice(0)]
    })
    console.log("mount props")
    console.log(this.props.initialPlayers)
    console.log("mount state")
    console.log(this.state.players)
    // this.chunking()

  }

  componentDidUpdate() {
    // console.log(this.state.players)
  }

  render() {
    var counter = 0
    // var heats = this.state.chunked.map(function(heat) {
    //           counter ++
    //           return <Heat key={counter} nr={counter} markWinner={this.markWinner} players={heat}/>
    //         }.bind(this))

    console.log("render1")
    console.log(this.state.players)
    var players = this.state.players.slice(0)
    console.log("render2")
    console.log(this.state.players)
    var heats = players.map(function(subArr) {
      counter ++
      return <Heat key={counter} nr={counter} markWinner={this.markWinner} players={subArr}/>
    }.bind(this))
    console.log("render2")
    console.log(this.state.players)


    return (
      <section>
        <div className="box content">
          {heats}
        </div>
      </section>
    );
  }
}



