import React, { Component } from 'react';

export class Hero extends Component {
  render() {
    return (
      <section className="hero is-primary">
        <div className="hero-body">
          <div className="container">
            <h1 className="title">
              Mariokart tournament
            </h1>
            <h2 className="subtitle">
              Let's gooooooo!
            </h2>
          </div>
        </div>
      </section>
    );
  }
}



