import React, { Component } from 'react';

export class Player extends Component {
  constructor(props) {
    super(props);

    this.markWinner = this.markWinner.bind(this)
  }


  markWinner() {
    this.props.markWinner(this.props.heatId, this.props.player)
  }


  render() {
    return (
      <div className="box" onClick={this.markWinner}>
        {this.props.player}
      </div>
    );
  }
}



