import React, { Component } from 'react';

import 'bulma/css/bulma.css'
import './App.css';

import { Hero } from './components/Hero.js';
import { Button } from './components/Button.js';
import { NewGameForm } from './components/NewGameForm.js';
import { CurrentGame } from './components/CurrentGame.js';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
     showNewGameForm: false,
     initialPlayers: [],
    };

    this.handleStartNewGameClick = this.handleStartNewGameClick.bind(this)
    this.handleSubmitPlayer = this.handleSubmitPlayer.bind(this)
  }

  handleStartNewGameClick() {
    var newState = this.state.showNewGameForm ? false : true

    this.setState({
      showNewGameForm: newState,
    })

    if (newState) {
      this.setState({
        initialPlayers: []
      })
    }
  }

  handleSubmitPlayer(playerName) {
    this.setState({
      initialPlayers:[...this.state.initialPlayers, playerName]
    })
  }

  render() {
    var newGameForm = this.state.showNewGameForm ? <NewGameForm onSubmitPlayer={this.handleSubmitPlayer} players={this.state.initialPlayers}/> : ""
    var buttonText = this.state.showNewGameForm ? "Start racing!" : "Start new game!"

    var currentGame = !this.state.showNewGameForm && this.state.initialPlayers.length > 4 ? <CurrentGame initialPlayers={this.state.initialPlayers}/> : ""

    return (
      <div className="App">
        <Hero/>
        <div className="container">
          <div className="section">
            <div className="has-text-centered">
              <Button classes="is-large is-rounded" onClick={this.handleStartNewGameClick}>{buttonText}</Button>
            </div>
          </div>
          <div className="columns">
            <div className="column">
              <div className="content">
                <h2>Playing with:</h2>
                {
                  this.state.initialPlayers.map(function(player) {
                    var comma = this.state.initialPlayers[this.state.initialPlayers.length-1] === player ? "" : ", "
                    return <strong key={player}>{player}{comma}</strong>
                  }.bind(this))
                }
              </div>
            </div>
            {newGameForm}
          </div>

          {currentGame}
        </div>
        <br/>
        <br/>
      </div>
    );
  }
}

export default App;



